body {
    background-color: #0078c236;
    color: #ffffffea;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    
    // prevent-select
    -webkit-user-select: none;
    /* Safari */
    -ms-user-select: none;
    /* IE 10 and IE 11 */
    user-select: none;
    /* Standard syntax */
}

h1 {
    font-size: 40px;
    margin-top: 3vh;
    color: #fff;
}

h2 {
    font-size: 25px;
    font-weight: bold;
    margin-top: 1rem;
}

h3 {
    font-size: 20px;
    font-weight: bold;
    // margin-top: 1rem;
}

h2>span.highlight,
h3>span.highlight {
    color: #ee5a1a;
}

p {
    font-size: 18px;
    margin-bottom: 20px;
    line-height: 1.5;
}

.highlight {
    font-size: 120%;
    font-weight: bold;
    color: #95b5c9;
    white-space: nowrap;
}

.little-span {
    white-space: nowrap;
    font-size: 98%; 
    // font-weight: normal;
    font-style: bold
}
    
.grey-span {   
    color: #e4bbaaef;
}

.white-span {   
    color: #ffffffd4;
}
.orange-span {   
    color: #dd906e;
}

.days-off-incr-button {
    color: #ffffff86;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 18px;
    font-size: 100%;
    color: #ffffff86;
    opacity: 1;
    transition: opacity 0.1s ease-in-out;
    cursor: pointer;
}

.days-off-incr-button:hover {
    opacity: .55;
}

.center {
    margin: auto;
    padding: 3vh;
    padding-bottom: 0;
    border: 2px solid #e0612a;
    background-color: #282c34;

}
.center-parent {
    margin: auto;
    width: 70%;
    max-width: 600px;
    margin-top:1vh;
    margin-bottom:7vh;
    border: 2vh solid #fc4d0200;
    border-radius: 10px;
    background-color: #282c34;

}

img {
    display: block;
    margin: auto;
    width: 80%;
    max-width: 800px;
    border: 2px solid #6b758a;
}

.img-text {
    font-size: 80%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: #ffffff;
    font-style: italic;
    line-height: 100%;
    width: 80%;
    margin:auto;
    padding-top: 1rem;
}

.draggables {
    cursor: pointer;
    font-size: 18px
}

.draggables:hover {
    cursor: ew-resize;
}
.draggables:active {
    cursor: ew-resize;
}

