.loader {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: #fc4c02;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    animation: spin 1s ease-in-out infinite;
    margin:auto;
    padding: 1rem;
    margin-top: 2rem;
    margin-bottom: 2rem;

}

.mileage-report-text {
    margin-top: 1rem;
}

.report-slider {
    margin-top: 1rem;

}

.h2-highlight {
    color: #e6652e;
    font-size: 28px;
}

.h3-highlight {
    color: #e6652e;
    font-size: 22px;
}

.orange {
    color: #ffffffe1;
    font-weight: light;
}

.header-pointers {
    font-style: italic;
    font-weight: 100;
}

.no-break {
    white-space: normal;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}